/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../../../base/config';
    @use './../../../../base/typography';
    // @use './../../../../components/button/style';

    html{
        width: 100%;
        height: auto;
        overflow: inherit;
        overflow-x: hidden;
        background-color: #FFFFFF;
        body{        
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            #root{
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }// root

            /*--- BANNER ---*/
            #banner-about{
                width: 100%;
                padding: 0;
                .content-banner{
                    width: 100%;
                    height: 70vh;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .bg-banner-about{
                        width: 100%;
                        height: 100%;
                        position: relative;
                        top: 0;
                        left: 0;
                        z-index: -10;
                        overflow: hidden;
                        background-color: #EDEDED;
                        &::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            z-index: 10;
                            background-color: rgba(255, 255, 255, 0.5);
                        }
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            object-position: center center;
                        }
                    }
                    .text-banner-about{
                        width: 85%;
                        height: 80%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        z-index: 20;
                    }
                }
            }// banner
            
            /*--- ABOUT ---*/
            #about-h{
                width: 100%;
                padding: 0;
                .content-about{
                    width: 100%;
                    height: 80vh;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    align-items: center;
                    .box-about{
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        width: 350px;
                        height: 450px;
                        background-color: #FFFFFF;
                        border-radius: 10px;
                        transition: all 0.6s;
                        border: 1px solid #2577f1;
                        .text-about{
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            h5, p{
                                text-align: center;
                            }
                            h5{
                                color: #2577f1;
                                font-weight: bold;
                                font-size: 2.5rem;
                            }
                            p{
                                padding: 10px 0;
                                width: 90%;
                                font-size: 1rem;
                                letter-spacing: 2px;
                            }
                        }
                        &:hover{
                            transition: all 0.6s;
                            transform: translateY(-8px);
                            border-bottom: 7px solid #2577f1;
                        }
                    }
                }
            }// about

            /*--- COUNTER ---*/
            #counter{
                width: 100%;
                padding: 20px 0;
                background-color: #FFFFFF;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .content-counter{
                    width: 90%;
                    height: 50vh;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    align-items: center;
                    .card-type-2{
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        width: 300px;
                        height: 250px;
                        background-color: #FFFFFF;
                        border-radius: 10px;
                        transition: all 0.6s;
                        border: 1px solid #2577f1;
                        .icon{
                            font-size: 3rem;
                            padding: 10px 0;
                            color: #2577f1;
                        }
                        .text{
                            font-size: 1.5rem;
                            padding: 10px 0;
                            font-weight: bold;
                            text-align: center;
                        }
                        .counter{
                            font-size: 2rem;
                            padding: 10px 0;
                            font-weight: bold;
                            color: #2577f1;
                        }
                        &:hover{
                            transition: all 0.6s;
                            transform: translateY(-8px);
                            border-bottom: 7px solid #2577f1;
                        }
                    }
                }
            }// counter

            /*--- TEAM ---*/
            #team{
                width: 100%;
                height: auto;
                padding: 0;
                .content-team{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    padding: 30px 0;
                    h1, h5{
                        margin: 20px 0;
                    }
                    h1{
                        color: #2577f1;
                    }
                    .team-cards{
                        width: 80%;
                        height: 100%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        flex-wrap: wrap;
                        .link-card{
                            text-decoration: none;
                            .card-type-3{
                                width: 350px;
                                height: 350px;
                                border: 1px solid #EDEDED;
                                margin: 30px 0;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                                position: relative;
                                z-index: 0;
                                transition: 0.3s all;
                                .img-team{
                                    width: 150px;
                                    height: 150px;
                                    border-radius: 50%;
                                    margin: 30px 0;
                                }
                                .text-team{
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    align-items: center;
                                    h5, p{
                                        color: #000000;
                                    }
                                }
                                &:hover{
                                    z-index: 1;
                                    transition: 0.3s all;
                                    border: none;
                                    box-shadow: 0 8px 50px rgba(37, 119, 241, 0.2);
                                    transform: scale(1.05);
                                }
                            }
                        }
                    }
                }
            }// team
        }// body
    }// html