    text-align: justify;
    color: #ffffff;
  }
  a.line-hover {
    position: relative;
  }
  a.line-hover::before {
    content: "";
    position: absolute;
    width: 0%;
    bottom: -3px;
    left: 0;
    height: 3px;
    background-color: #ffffff;
    transition-duration: 0.8s;
  }
  a.line-hover:hover::before, a.line-hover.active::before {
    width: 100%;
  }
  a.title {
    font-size: 21px;
