/*----------------
    NORMALIZE
----------------*/
    /*----------------
    RESOLUTIONS
    ----------------*/
    // PC
        $pc: 3550px;
    // LAPTOP
        $laptop: 1550px;
    // TABLET
        $tablet: 960px;
    // MOVIL
        $movil: 500px;
    /*----------------
    FONST STYLE
    ----------------*/
    // TYPOGRAPHY
    @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
    @import url('https://fonts.googleapis.com/css2?family=Shippori+Mincho&display=swap');
    $font-1: 'Arial',sans-serif;
    $font-2: 'Arial',sans-serif;
    // FONTS SIZE
    $title: 40px;
    $subtitle: 33px;
    $text-ultrabig: 30px;
    $text-big: 26px;
    $text-medium: 15px;
    $text-small: 13px;
    $text-ultrasmall: 10px;
    /*----------------
    CLASS
    ----------------*/
    // FLEX     
    .flex{
        display: flex;
        justify-content: center;
        align-items: center;
        &.column{
            flex-direction: column;
        }
        &.row{
            flex-direction: row;
        }
        &.j-start{
            justify-content: flex-start;
        }
        &.j-end{
            justify-content: flex-end;
        }
        &.j-space-a{
            justify-content: space-around;
        }
        &.j-space-b{
            justify-content: space-between;
        }
        &.j-space-e{
            justify-content: space-evenly;
        }
        &.a-start{
            align-items: flex-start;
        }
        &.a-end{
            align-items: flex-end;
        }
        &.a-space-a{
            align-items: space-around;
        }
        &.a-space-b{
            align-items: space-between;
        }
        &.a-space-e{
            align-items: space-evenly;
        }
        &.wrap{
            flex-wrap: wrap;
        }
    }
    // IMG 
    div{
        &.img{
            height: auto;
            width: 100%;
            &.bg{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
                &::before{
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.274);
                }
                &.g-45{
                    &::before{
                        background-color: rgba(0, 0, 0, 0.45);
                    } 
                }
                &.g-65{
                    &::before{
                        background-color: rgba(0, 0, 0, 0.65);
                    } 
                }
            }
        }
    }
    .size{
        &.w-100{
            width: 100%;
        }
        &.w-95{
            width: 95%;
        }
        &.w-90{
            width: 90%;
        }
        &.w-85{
            width: 85%;
        }
        &.w-80{
            width: 80%;
        }
        &.w-75{
            width: 75%;
        }
        &.w-70{
            width: 70%;
        }
        &.w-65{
            width: 65%;
        }
        &.w-50{
            width: 50%;
        }
        &.w-25{
            width: 25%;
        }
    }
    // DATATABLE
    .dataTables_wrapper {      
        .datatable{
            tbody{
                background-color: #fbfbfb;
                tr{
                    background-color: transparent !important;
                    p{
                        padding: 8px 0 !important;
                        text-transform: lowercase;
                    }
                    img{
                        height: 150px;
                    }
                    &:hover{
                        background-color: #f0f0f0 !important;
                    }
                }
            }
        }
        .dataTables_paginate{
            .paginate_button{
                &:hover,
                &.current,
                &.current:hover{
                    background: #121212 !important;
                    color: #fff !important;
                    border: 1px solid #121212 !important;
                }
                
            }
        }
    }
    // BUTTONS
    .button{
        position: relative;
        z-index: 20;
        padding: 6px 30px;
        text-transform: uppercase;
        text-decoration: none;
        border: 2px solid rgb(0, 0, 0);
        background-color: rgba(0, 0, 0);
        box-sizing: border-box;
        transition: 0.5s;
        width: max-content;
        cursor: pointer;
        i{
            font-size: 13px;
            color: rgb(255, 255, 255);
        }
        &:hover{
            border: 2px solid rgb(0, 0, 0);
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            i{
                color: rgb(0, 0, 0);
            }
        }
        &.active{
            border: 2px solid rgb(0, 0, 0);
            i{
                color: rgb(0, 0, 0);
            }
            &::before{
                transform: scaleX(1);
                transition: transform 0.5s;
                transform-origin: right;
            }
            &::after{
                transform: scaleX(1);
                transition: transform 0.5s;
                transform-origin: left;
            }
        }
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 51%;
            height: 100%;
            background: rgb(255, 255, 255);
            z-index: -1;
            transform: scaleX(0);
            transition: transform 0.5s;
            transform-origin: left;
        }
        &:hover::before{
            transform: scaleX(1);
            transition: transform 0.5s;
            transform-origin: right;
        }
        &::after{
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            width: 51%;
            height: 100%;
            background: rgb(255, 255, 255);
            z-index: -1;
            transform: scaleX(0);
            transition: transform 0.5s;
            transform-origin: right;
        }
        &:hover::after{
            transform: scaleX(1);
            transition: transform 0.5s;
            transform-origin: left;
        }
        &.type-2{
            border: 2px solid rgb(255, 255, 255);
            background-color: rgba(0, 0, 0);
            i{
                color: rgb(255, 255, 255);
            }
            &:hover{
                border: 2px solid rgb(255, 255, 255);
                i{
                    color: rgb(0, 0, 0);
                }
            }
        
            &::before{
                background: rgb(255, 255, 255);
            }
            &::after{
                background: rgb(255, 255, 255);
            }
            &.active{
                border: 2px solid rgb(255, 255, 255);
                i{
                    color: rgb(0, 0, 0);
                }
                &::before{
                    background: rgb(255, 255, 255);
                    transform: scaleX(1);
                    transition: transform 0.5s;
                    transform-origin: right;
                }
                &::after{
                    background: rgb(255, 255, 255);
                    transform: scaleX(1);
                    transition: transform 0.5s;
                    transform-origin: left;
                }
            }
        }
        &.type-3{
            border: 2px solid rgb(0, 0, 0);
            background-color: rgb(255, 255, 255);
            i{
                color: rgb(0, 0, 0);
            }
            &:hover{
                border: 2px solid rgb(255, 255, 255);
                i{
                    color: rgb(0, 0, 0);
                }
            }
        
            &::before{
                background: rgb(255, 255, 255);
            }
            &::after{
                background: rgb(255, 255, 255);
            }
            &.active{
                border: 2px solid rgb(0, 0, 0);
                i{
                    color: rgb(0, 0, 0);
                }
                &::before{
                    background: rgb(255, 255, 255);
                    transform: scaleX(1);
                    transition: transform 0.5s;
                    transform-origin: right;
                }
                &::after{
                    background: rgb(255, 255, 255);
                    transform: scaleX(1);
                    transition: transform 0.5s;
                    transform-origin: left;
                }
            }
        }
    }
    // INPUTS
    .input{
        position: relative;
        width: 100%;
        max-width: 400px;
        height: 50px;
        overflow: hidden;
        margin-top: 25px;
        input{
            width: 100%;
            height: 100%;
            background: none;
            color: rgba(92, 92, 92, 0.918);
            border: none;
            outline: 0px;
            padding-top: 10px;
            &:focus{
                border-bottom: solid 2px #000;
            }
        }
        label{
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            border-bottom: 1px solid #000;
            &::after{
                content: '';
                position: absolute;
                left: 0;
                bottom: -1px;
                width: 100%;
                height: 100%;
                border-bottom: 3px solid rgb(200, 15, 40);
                transform: translateX(-100%);
                transition: all 0.3s ease;
            }
            span{
                position: absolute;
                bottom: 5px;
                left: 0;
                transition: all 0.3s ease;
                font-size: 16px;
                color: #000;
            }
        }
        input:focus + label span, input:valid + label span, input:disabled + label span{
            transform: translateY(-150%);
            font-size: 14px;
            color: rgb(200, 15, 40);
        }
        input:focus + label:after, input:valid + label:after{
            transform: translateX(0%);
        }
        &.date{
            input{
                height: 30px;
                padding-top: 5px;
            }
        }
    }
    
    .modal{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.712);
        display: flex;
        justify-content: center;
        align-items: flex-start;
        z-index: 1500;
        pointer-events: none;
        transition: all 0.8s ease;
        opacity: 0;
        overflow-y: auto;
        &.disable{
            display: none;
        }
        .content{
            padding: 25px;
            background: #fff;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            margin-top: 15px;
            margin-bottom: 50px;
            .fa-skull,
            .fa-user-edit,
            .fa-user-plus,
            .fa-plug,
            .fa-code{
                font-size: 50px;
                color: #f50057;
                margin: 15px;
            }
            .fa-user-edit,
            .fa-user-plus,
            .fa-code{
                margin-bottom: -15px;
            }
            .text-modal{
                margin-top: 5px;
                color: #000;
                font-size: 16px;
                margin-bottom: 15px;
                width: 300px;
                text-align: center;
            }
            .container-btns{
                margin-top: 15px;
                width: 300px;
                &.add{
                    margin-top: 35px;
                }
            }
            select{
                border: 1px solid #000;
                position: relative;
                width: 100%;
                max-width: 400px;
                height: 40px;
                overflow: hidden;
                margin-top: 25px;
                font-size: 16px;
                margin-bottom: 15px;
            }
            .container-inputs{
                width: 650px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin: 25px 0;
                .input,select{
                    margin: 0;
                    max-width: 280px;
                    input{
                        text-transform: lowercase;
                    }
                    label{
                        span{
                            a{
                                margin-left: 13px;
                                z-index: 1000;
                                pointer-events: all;
                                i{
                                    color: #f50057;
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
                .upload-area{
                    width: 100%;
                    height: 160px;
                    border: 2px dashed #d5d5e1;
                    color: #c8c9dd;
                    position: relative;
                    background-color: #eeeeee59;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    user-select: none;
                    span{
                        font-size: 18px;
                        text-align: center;
                    }
                    .select{
                        color: #f50057;
                        margin-left: 7px;
                        cursor: pointer;
                    }
                    input{
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        opacity: 0;
                    }
                }
                .container-preview{
                    width: auto;
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                    flex-wrap: wrap;
                    position: relative;
                    height: auto;
                    margin-top: -20px;
                    max-height: 300px;
                    overflow-y: auto;
                    .image{
                        height: 185px;
                        width: 260px;
                        border-radius: 5px;
                        box-shadow: 0 0 5px rgba(0,0,0, 0.15);
                        overflow: hidden;
                        margin-right: 7px;
                        position: relative;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                        span{
                            position: absolute;
                            top: -4px;
                            right: 5px;
                            z-index: 1000;
                            cursor: pointer;
                            font-size: 22px;
                            color: rgb(0, 0, 0);
                        }
                        &:nth-child(4n){
                            margin-right: 0;
                        }
                        &.first{
                            border: 2px #f50057 solid;
                            label{
                                width: 100%;
                                bottom: 0;
                                position: absolute;
                                font-size: 12px;
                                color: #f50057;
                                z-index: 10000;
                                text-align: center;
                            }
                        }
                    }
                }
                textarea{
                    width: 100%;
                    margin-top: 10px;
                    padding: 5px;
                    resize: none;
                    height: 150px;
                }
                .container-map{
                    width: 100%;
                    height: 250px;
                    overflow: hidden;
                    position: relative;
                    .map {
                        position: relative;
                        height: 100%;
                        width: 100%;
                    }
                }
                
            }
            .code {
                height: 400px;
                width: 100%;
                resize: none;
                border: 1px solid #000;
                &.edit{
                    min-width: 600px;
                }
            }
        }
        &.active{
            opacity: 1;
            pointer-events: all;
        }
    }
      
    /*--------------------------
    RESOLUTION CONTROLLER
    --------------------------*/
    //RESOLUTION PC
    html{
        body{
            #root{
                min-height: 100vh;
                overflow-x: hidden;
                justify-content: flex-start;
                .container {
                    position: relative;
                    width: 100%;
                    height: 100vh;
                    .navigation {
                        position: fixed;
                        width: 280px;
                        height: 100%;
                        background: #2577f1;
                        border-left: 10px solid #2577f1;
                        transition: 0.5s;
                        overflow: hidden;
                        .logo {
                            width: 100%;
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                            margin-bottom: -30px;
                            margin-top: 10px;
                            img{
                                width: 70%;
                                margin-left: 25px;
                                height: auto;
                              }
                        }
                        ul {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            li, #list {
                                position: relative;
                                width: 100%;
                                list-style: none;
                                border-top-left-radius: 30px;
                                border-bottom-left-radius: 30px;
                                cursor: pointer;
                                &:hover,
                                &.hovered {
                                  background-color: #fff;
                                  a, .new-icon, .new-title {
                                    color: #2577f1;
                                    &::before {
                                        content: "";
                                        position: absolute;
                                        right: 0;
                                        top: -50px;
                                        width: 50px;
                                        height: 50px;
                                        background-color: transparent;
                                        border-radius: 100%;
                                        box-shadow: 35px 35px 0 10px #fff;
                                      }
                                      &::after {
                                        content: "";
                                        position: absolute;
                                        right: 0;
                                        bottom: -50px;
                                        width: 50px;
                                        height: 50px;
                                        background-color: transparent;
                                        border-radius: 50%;
                                        box-shadow: 35px -35px 0 10px #fff;
                                        pointer-events: none;
                                      }
                                  }
                                }
                                &:nth-child(1) {
                                    margin-bottom: 40px;
                                    pointer-events: none;
                                }
                                a {
                                    position: relative;
                                    display: block;
                                    width: 100%;
                                    display: flex;
                                    text-decoration: none;
                                    color: #fff;
                                    pointer-events: none;
                                    transition-duration: 0.5s;
                                    .icon {
                                        position: relative;
                                        display: block;
                                        width: 60px;
                                        height: 60px;
                                        line-height: 75px;
                                        text-align: center;
                                        svg {
                                            font-size: 1.4rem;
                                            margin-bottom: 5px;
                                          }
                                    }
                                    .title {
                                        position: relative;
                                        display: block;
                                        padding: 0 10px;
                                        height: 60px;
                                        line-height: 60px;
                                        text-align: start;
                                        white-space: nowrap;
                                        font-size: 16px;
                                      }
                                  }
                            }
                        }
                        &.active {
                            width: 80px;
                            ul {
                                li {
                                    a {
                                        .icon {
                                            margin-right: 20px;
                                            margin-left: 25px;
                                        }
                                      }
                                }
                            }
                        }
                    }
                    .main {
                        position: absolute;
                        width: calc(100% - 280px);
                        left: 280px;
                        min-height: 100vh;
                        background: #fff;
                        transition: 0.5s;
                        overflow-x: hidden;
                        &.active {
                            width: calc(100% - 80px);
                            left: 80px;
                        }
                        .topbar {    
                            width: calc(100% - 317px);
                            height: 60px;
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            padding: 0 10px;
                            position: fixed;
                            top: 0;
                            left: 280px;
                            z-index: 1000;
                            background: #FFF;
                            transition: 0.5s;
                            &.active {
                                width: calc(100% - 116px);
                                left: 80px;
                            }
                            .toggle {
                                position: relative;
                                width: 60px;
                                height: 60px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                font-size: 2.5rem;
                                cursor: pointer;
                                .ham {
                                    -webkit-tap-highlight-color: transparent;
                                    transition: transform 400ms;
                                    -moz-user-select: none;
                                    -webkit-user-select: none;
                                    -ms-user-select: none;
                                    user-select: none;
                                    width: 50px;
                                    height: 50px;
                                  }
                                  .hamRotate180.active {
                                    transform: rotate(180deg);
                                  }
                                  .line {
                                    fill: none;
                                    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
                                    stroke: #0a0a0a;
                                    stroke-width: 5.3;
                                    stroke-linecap: round;
                                  }
                                  .ham6 .top {
                                    stroke-dasharray: 40 172;
                                  }
                                  .ham6 .middle {
                                    stroke-dasharray: 40 111;
                                  }
                                  .ham6 .bottom {
                                    stroke-dasharray: 40 172;
                                  }
                                  .ham6.active .top {
                                    stroke-dashoffset: -132px;
                                  }
                                  .ham6.active .middle {
                                    stroke-dashoffset: -71px;
                                  }
                                  .ham6.active .bottom {
                                    stroke-dashoffset: -132px;
                                  }
                            }
                            .user {
                                position: relative;
                                width: 45px;
                                height: 45px;
                                border-radius: 50%;
                                overflow: hidden;
                                cursor: pointer;
                                margin-right: 15px;
                                img {
                                    position: absolute;
                                    top: 0;
                                    left: 0;
                                    width: 100%;
                                    height: 100%;
                                    object-fit: cover;
                                  }
                            }
                            .name-user{
                                width: max-content;
                                margin-right: 35px;
                                margin-left: 0;
                            }
                        }
                        main{
                            width: 100%;
                            height: 100%;
                            iframe{
                                width: 100%;
                                height: 100%;
                            }
                        }
                      }
                }
                section{
                    &.head{
                        padding-bottom: 0;
                        p{
                            color: rgba(97, 97, 97, 0.849);
                        }
                        h3{
                            color: rgb(200, 15, 40);
                        }
                    }
                }
                .datatable{
                    .actions-user{
                        p{
                            display: flex;
                            justify-content: space-around;
                            align-items: center;
                            i{
                                cursor: pointer;
                                font-size: 16px;
                                &.fa-trash{
                                    color: #f50057;
                                }
                            }
                        }
                    }
                }
            }
            #loader{
                width: 50px;
                height: 50px;
                position: fixed;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                margin: auto;
                opacity: 0;
                pointer-events: none;
                transition: all 0.5s ease;
                z-index: 5000;
                &.active{
                    opacity: 1;
                    pointer-events: all;
                }
                &::before{
                    content: '';
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba(255, 255, 255, 0.74);
                }
                .loader-outter {
                    position: absolute;
                    border: 4px solid #f50057;
                    border-left-color: transparent;
                    border-bottom: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    -webkit-animation: loader-1-outter 1s cubic-bezier(.42, .61, .58, .41) infinite;
                    animation: loader-1-outter 1s cubic-bezier(.42, .61, .58, .41) infinite;
                }
                
                .loader-inner {
                    position: absolute;
                    border: 4px solid #f50057;
                    border-radius: 50%;
                    width: 40px;
                    height: 40px;
                    left: calc(50% - 20px);
                    top: calc(50% - 20px);
                    border-right: 0;
                    border-top-color: transparent;
                    -webkit-animation: loader-1-inner 1s cubic-bezier(.42, .61, .58, .41) infinite;
                    animation: loader-1-inner 1s cubic-bezier(.42, .61, .58, .41) infinite;
                }
                @-webkit-keyframes loader-1-outter {
                    0% {
                        -webkit-transform: rotate(0deg);
                        transform: rotate(0deg);
                    }
                    100% {
                        -webkit-transform: rotate(360deg);
                        transform: rotate(360deg);
                    }
                }
                
                @keyframes loader-1-outter {
                    0% {
                        -webkit-transform: rotate(0deg);
                        transform: rotate(0deg);
                    }
                    100% {
                        -webkit-transform: rotate(360deg);
                        transform: rotate(360deg);
                    }
                }
                
                @-webkit-keyframes loader-1-inner {
                    0% {
                        -webkit-transform: rotate(0deg);
                        transform: rotate(0deg);
                    }
                    100% {
                        -webkit-transform: rotate(-360deg);
                        transform: rotate(-360deg);
                    }
                }
                
                @keyframes loader-1-inner {
                    0% {
                        -webkit-transform: rotate(0deg);
                        transform: rotate(0deg);
                    }
                    100% {
                        -webkit-transform: rotate(-360deg);
                        transform: rotate(-360deg);
                    }
                }
            }
        }
    }
    
    // RESOLUTION LAPTOP
    @media only screen and (max-width: $laptop){
        #root{
                aside{
                    &.active{
                        width: 20%;
                    }
                }
                main{
                    &.active{
                        width: 80%;
                    }
                }
            }
    }
    
    // RESOLUTION TABLET
    @media only screen and (max-width: $tablet){  
        #root{
                overflow: hidden;
                aside{
                    position: absolute;
                    top: 0;
                    left: -300px;
                    transition-duration: 0.8s;
                    width: 300px;
                    &.active{
                        left: 0;
                        width: 300px;
                    }
                }
                main{
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    transition-duration: 0.8s;
                    &.active{
                        left: 300px;
                    }
                }
            }
    }
    
    // RESOLUTION MOVIL
    @media only screen and (max-width: $movil){  
        #root{
                main{
                        .rigth{
                          overflow-y: auto;
                          width: 100%;
                          justify-content: flex-start;
                          &::before{
                            top: -180px;
                            width: 100%;
                            height: 300px;
                          }
                          .contact{
                            margin-top: 20px;
                          }
                          .content{
                            margin-top: 50px;
                            h1{
                              margin-bottom: 0px;
                            }
                            form{
                              width: 90%;
                              input{
                                width: 90%;
                              }
                            }
                          }
                          footer{
                            width: 90%;
                            margin-bottom: 25px;
                            p{
                              width: 100%;
                              text-align: center;
                            }
                          }
                        }
                    }
        }
    }