/*--------------------------
        GENERAL
--------------------------*/
// PRIMARY
    $P-100: #ffffff;
    $P-25: rgba(255, 0, 21, 0.945);
// SECONDARY
    $S-100: #17b283;
// TERTIARY 
    $T-100: #132922;
// COMPLEMENTARY  
    // #1
        $C1-200: #4b4b4b;
        $C1-150: #b6b6b6;
        $C1-100: #E8E8E8;
        $C1-75: #F9F6F7;
        $C1-50: #fafafa;
        $C1-25: #FDFDFD;
        $C1-0: #FFFFFF;
    // #2
        $C2-150: #000000;
        $C2-100: #0a0a0a;
        $C2-75: #111111;
        $C2-50: #1a1a1a;
        $C2-25: #252525;
        $C2-0: #504f4f;
/*--------------------------
        MODE CONTROLLER
--------------------------*/
@media (prefers-color-scheme: dark){
    // PRIMARY
        $P-100: #000000;
        $P-25: rgba(228, 28, 45, 0.945);
    // SECONDARY
        $S-100: #17b283;
    // TERTIARY 
        $T-100: #132922;
    // COMPLEMENTARY  
        // #1
            $C1-200: #4b4b4b;
            $C1-150: #b6b6b6;
            $C1-100: #E8E8E8;
            $C1-75: #F9F6F7;
            $C1-50: #fafafa;
            $C1-25: #FDFDFD;
            $C1-0: #FFFFFF;
        // #2
            $C2-150: #000000;
            $C2-100: #0a0a0a;
            $C2-75: #111111;
            $C2-50: #1a1a1a;
            $C2-25: #252525;
            $C2-0: #504f4f;
    
    html{
        background-color: #111111;
    }
}