/*--------------------------
        IMPORTS
--------------------------*/
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Shippori+Mincho&display=swap");
/*--------------------------
        GENERAL
--------------------------*/
* {
  margin: 0;
  padding: 0;
}

html {
  min-width: 100vw;
  min-height: 100vh;
}
html body {
  position: static;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
html body #root {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
html body div img {
  width: 100%;
  height: auto;
}
html body section {
  width: 100%;
  padding: 35px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
html body section.row {
  flex-direction: row;
}
html body section.column {
  flex-direction: column;
}
html body input[type=number] {
  -webkit-appearance: textfield !important;
  margin: 0;
  -moz-appearance: textfield !important;
}

/*--------------------------
        GENERAL
--------------------------*/
/*--------------------------
        GENERAL
--------------------------*/
/*--------------------------
        MODE CONTROLLER
--------------------------*/
@media (prefers-color-scheme: dark) {
  html {
    background-color: #111111;
  }
}
/*----------------
    FONST STYLE
----------------*/
* {
  font-family: "Arial", sans-serif;
}

/*--------------------------
    RESOLUTION CONTROLLER
--------------------------*/
@media only screen and (max-width: 3550px) {
  h1 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-size: 48px;
    margin-bottom: 15px;
  }
  h2 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-size: 36px;
    margin-bottom: 7px;
  }
  h3 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-size: 36px;
  }
  h4 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-style: italic;
    font-size: 36px;
  }
  h5 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-size: 26px;
  }
  h6 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-style: italic;
    font-size: 26px;
  }
  th {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
  }
  p {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
    letter-spacing: normal;
    line-height: 1.8;
  }
  li {
    font-family: "Arial", sans-serif;
    font-size: 15px;
  }
  a {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
    color: #ffffff;
  }
  a.line-hover {
    position: relative;
  }
  a.line-hover::before {
    content: "";
    position: absolute;
    width: 0%;
    bottom: -3px;
    left: 0;
    height: 3px;
    background-color: #ffffff;
    transition-duration: 0.8s;
  }
  a.line-hover:hover::before, a.line-hover.active::before {
    width: 100%;
  }
  a.title {
    font-size: 21px;
  }
  button {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
  }
  textarea {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
  }
  input {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
  }
  label {
    font-family: "Arial", sans-serif;
    font-size: 15px;
    text-align: justify;
  }
  i {
    font-family: "Arial", sans-serif;
    font-size: 19px;
    text-align: justify;
  }
  span {
    font-family: "Arial", sans-serif;
    font-size: 10px;
    text-align: justify;
  }
}
@media only screen and (max-width: 500px) {
  h1 {
    font-family: "Arial", sans-serif;
    font-weight: bolder;
    font-size: 28px;
    margin-bottom: 15px;
  }
}
