    justify-content: center;
    align-items: center;
    width: 100%;
    align-items: flex-end;
    justify-content: space-around;
    text-align: center;
    z-index: 7;
  }
  #login .login-container .panels-container .panel .content {
    color: #FFFFFF;
    transition: 0.9s 0.7s ease-in-out;
  }
  #login .login-container .panels-container .panel .content h3 {
    font-weight: 600;
    font-size: 25px;
    line-height: 1;
    margin-bottom: 25px;
  }
  #login .login-container .panels-container .panel .content p {
    font-size: 25px;
    text-align: center;
    padding: 10px 0;
  }
  #login .login-container .panels-container .panel .content .btn.transparent {
    margin: 0;
    background: none;
    border: 2px solid #FFFFFF;
    width: 120px;
    height: 50px;
    border-radius: 10px;
    font-weight: 600;
    font-size: 1em;
    color: #FFFFFF;
    text-align: center;
    cursor: pointer;
    margin-bottom: 35px;
  }
  #login .login-container .panels-container .panel .content .btn.transparent:hover {
    background-color: #FFFFFF;
    border: 2px solid #2577f1;
    color: #2577f1;
  }
  #login .login-container .panels-container .panel .image {
    width: 125%;
    transition: 1.1s 0.4s ease-in-out;
    position: relative;
    z-index: 10;
  }
  #login .login-container .panels-container .left-panel {
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 45%;
    height: 100%;
    pointer-events: all;
    transition-duration: 1.5s;
  }
  #login .login-container .panels-container .left-panel img {
    bottom: -5px;
  }
  #login .login-container .panels-container .right-panel {
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 45%;
    height: 100%;
    pointer-events: none;
    transition-duration: 1.5s;
    transform: translateX(80%);
  }
  #login .login-container.sign_up_mode .left-panel {
    pointer-events: none;
  }
  #login .login-container.sign_up_mode .left-panel .content, #login .login-container.sign_up_mode .left-panel .image {
    transform: translateX(-800px);
  }
  #login .login-container.sign_up_mode .right-panel {
    pointer-events: all;
  }
  #login .login-container.sign_up_mode .right-panel .content, #login .login-container.sign_up_mode .right-panel .image {
    transform: translateX(0px);
  }
  #login .login-container.sign_up_mode::before {
    transform: translate(100%, -50%);
    right: 50%;
  }
  #login .login-container.sign_up_mode .signin-signup {
    left: 35%;
  }
