@media only screen and (max-width: 3550px) {
  /*--- HEADER ---*/
  .header {
    width: 100%;
    height: 15vh;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header .information {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header .information .info-text {
    width: 100%;
    padding: 5px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header .information .info-text .info-sub-tex {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .header .information .info-text .info-sub-tex .icon {
    font-size: 1.2rem;
    color: #2577f1;
  }
  .header .information .info-text .info-sub-tex p {
    margin-left: 20px;
    font-size: 1rem;
    color: #000000;
    letter-spacing: 2px;
  }
  .header nav {
    width: 85%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header nav .logo {
    color: #000000;
    text-decoration: none;
  }
  .header nav ul {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  .header nav ul li {
    list-style: none;
  }
  .header nav ul li a {
    color: #2b2b2b;
    text-decoration: none;
    font-size: 17px;
    transition: all 0.2s;
    font-weight: bold;
  }
  .header nav ul li a:hover {
    transition: all 0.2s;
    color: #2577f1;
  }
  .header nav ul li:nth-last-child(1) a {
    transition: all 0.2s;
    color: #ffffff;
  }
  .header nav .btn-header {
    padding: 15px 20px;
    text-decoration: none;
    background-color: #2577f1;
    border: 1px solid #2577f1;
    border-radius: 10px;
    transition: all 0.2s;
    font-weight: bold;
  }
  .header nav .btn-header:hover {
    transition: all 0.2s;
    background-color: #FFFFFF;
    color: #2577f1;
  }
}
