@media only screen and (max-width: 960px) {
  /*--- HEADER ---*/
  .header {
    width: 100%;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header .information {
    display: none !important;
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header .information .info-text {
    width: 100%;
    padding: 5px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header .information .info-text .info-sub-tex {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .header .information .info-text .info-sub-tex .icon {
    font-size: 1.2rem;
    color: #2577f1;
  }
  .header .information .info-text .info-sub-tex p {
    margin-left: 20px;
    font-size: 1rem;
    color: #000000;
    letter-spacing: 2px;
  }
  .header nav {
    width: 85%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .header nav .nav-logo .logo {
    color: #000000;
    text-decoration: none;
  }
  .header nav .nav-items {
    position: absolute;
    top: 100px;
    left: 0;
    background-color: gray;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: -webkit-fill-available;
    transform: translateX(-100%);
    transition: 0.3s ease all;
    z-index: 20;
  }
  .header nav .nav-items li {
    list-style: none;
    z-index: 20;
  }
  .header nav .nav-items li a {
    color: #2b2b2b;
    text-decoration: none;
    font-size: 17px;
    transition: all 0.2s;
    font-weight: bold;
  }
  .header nav .nav-items li a:hover {
    transition: all 0.2s;
    color: #2577f1;
  }
  .header nav .nav-items li:nth-last-child(1) a {
    transition: all 0.2s;
    color: #ffffff;
  }
  .header nav .nav-items li .btn-header {
    padding: 15px 20px;
    text-decoration: none;
    background-color: #2577f1;
    border: 1px solid #2577f1;
    border-radius: 10px;
    transition: all 0.2s;
    font-weight: bold;
  }
  .header nav .nav-items li .btn-header:hover {
    transition: all 0.2s;
    background-color: #FFFFFF;
    color: #2577f1;
  }
  .header nav .nav-items.open {
    transform: translateX(0) !important;
  }
  .header nav .nav-toggle {
    display: flex !important;
    flex-direction: column;
    margin: 15px;
  }
  .header nav .nav-toggle span {
    width: 30px;
    height: 4px;
    background: black;
    margin-bottom: 5px;
    border-radius: 2px;
    transform-origin: 5px 0px;
    transition: all 0.2s linear;
  }
  .header nav .nav-toggle.open > span {
    transform: rotate(45deg) translate(0px, 0px);
  }
  .header nav .nav-toggle.open > span:nth-child(2) {
    display: none;
  }
  .header nav .nav-toggle.open > span:nth-child(3) {
    transform: rotate(-45deg) translate(-5px, 1px);
  }
}
