/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../../../base/config';
    @use './../../../../base/typography';
    //@use './../../../../components/button/style';

    html{
        width: 100%;
        height: auto;
        overflow: inherit;
        overflow-x: hidden;
        background-color: #FFFFFF;
        body{        
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            #root{
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
            }// root

            /*--- BANNER ---*/
            #banner-contact{
                width: 100%;
                .content-banner{
                    width: 100%;
                    height: 70vh;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .bg-banner-about{
                        width: 100%;
                        height: 100%;
                        position: relative;
                        top: 0;
                        left: 0;
                        z-index: -10;
                        overflow: hidden;
                        background-color: #EDEDED;
                        &::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            z-index: 10;
                            background-color: rgba(255, 255, 255, 0.5);
                        }
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            object-position: center center;
                        }
                    }
                    .text-banner-about{
                        width: 85%;
                        height: 80%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        z-index: 20;
                    }
                }
            }// banner

            /*--- DIRECTION ---*/
            #direction{
                width: 100%;
                height: 90vh;
                padding: 0;
                .content-direction{
                    width: 80%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .box-direction{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;
                        .text-direction{
                            width: 50%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            .text-sucursal{
                                width: 100%;
                                height: 100%;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: flex-start;
                                h5, p{
                                    padding: 10px 20px;
                                }        
                                p{
                                    font-size: 1rem;
                                }                    
                            } 
                        }
                        .img-direction{
                            width: 50%;
                            height: 100%; 
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: cover; 
                                object-position: center;
                            }
                        }                        
                    }
                }
            }// direction
            
            /*--- CONTACT ---*/
            #contact{
                width: 100%;
                padding: 0;
                background-color: #FFFFFF;
                .content-contact{
                    width: 100%;
                    height: 60vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    padding: 50px 0;
                    h5, h1{
                        color: #000000;
                    }
                    .content-icon-text{
                        width: 60%;
                        height: 40%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        .box-icon-text{
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            width: 300px;
                            height: 200px;
                            border-radius: 10px;
                            margin-top: 40px;
                            .icon{
                                font-size: 3rem;
                                margin: 20px 0;
                                color: #2577f1;
                            }
                            p{
                                color: #000000;
                                margin: 20px 0;
                                font-size: 1.2rem;
                                transition: all 0.3s;
                            }
                            &:hover{
                                transition: all 0.6s;
                                transform: translateY(-8px);
                                background-color: #EDEDED;
                                border-bottom: 4px solid #2577f1;
                            }
                        }
                    }
                    .content-form{
                        width: 60%;
                        height: 40%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        background-color: #FFFFFF;
                        form{
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            .content-text-form{
                                width: 100%;
                                height: 100%;
                                display: flex;
                                flex-direction: row;
                                justify-content: space-around;
                                align-items: center;
                                padding: 20px 0;
                                .content-input{
                                    width: 45%;
                                    height: 90%;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: space-between;
                                    align-items: center;
                                    input{
                                        width: 92%;
                                        padding: 10px 15px;
                                        border: none;
                                        outline: none;
                                        background-color: #FFFFFF;
                                        color: #000000;
                                        transition: all 0.3s;
                                        border: 1px solid #000000;
                                        &:focus{
                                            transition: all 0.3s;
                                            border: 1px solid #2577f1;
                                        }
                                    }
                                }
                                .content-textarea{
                                    width: 45%;
                                    height: 90%;
                                    textarea{
                                        width: 93%;
                                        height: 90%;
                                        resize: none;
                                        padding: 10px 15px;
                                        border: none;
                                        outline: none;
                                        background-color: #FFFFFF;
                                        color: #000000;
                                        transition: all 0.3s;
                                        border: 1px solid #000000;
                                        &:focus{
                                            transition: all 0.3s;
                                            border: 1px solid #2577f1;
                                        }
                                    }
                                }
                            }
                            .button-type-4{
                                padding: 15px 50px;
                                background-color: #2577f1;
                                border-radius: 10px;
                                text-decoration: none;
                                font-weight: bold;
                                border: 1px solid #2577f1;
                                transition: all 0.3s;
                                color: #FFFFFF;
                                &:hover{
                                    color: #2577f1;
                                    transition: all 0.3s;
                                    background-color: #FFFFFF;

                                }
                            }
                        }
                    }
                }
            }// contact
        }// body
    }// html