/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../../base/config';
    @use './../../../base/typography';

    html{
        width: 100%;
        height: auto;
        overflow: inherit;
        overflow-x: hidden;
        background-color: #FFFFFF;
        body{
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            #root{
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }// root

            /* WELCOME */
            #welcome{
                width: 100%;
                height: 100vh;
                padding: 0;
                .content-welcome{
                    width: 95%;
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    h1{
                        width: 50%;
                        text-align: center;
                        margin-bottom: 50px;
                        color: #2577f1;
                    }
                    .button-type-5{
                        width: 400px;
                        margin: 25px 0;
                        padding: 30px 10px;
                        border: 2px solid #2577f1;
                        border-radius: 10px;
                        color: #2577f1;
                        text-align: center;
                        text-decoration: none;
                        font-size: 1.4rem;
                        transition: all 0.4s;
                        font-weight: bold;
                        &:hover{
                            transition: all 0.4s;
                            background-color: #2577f1;
                            border: 2px solid #2577f1;
                            color: #FFFFFF;
                        }
                    }
                }
            }// welcome

            /* LOGIN */
            #login{
                width: 100%;
                height: 100vh;
                padding: 0;
                .login-monitor{
                    position: relative;
                    width: 95%;
                    height: 90%;
                    background-color: #FFFFFF;
                    overflow: hidden;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .sign-in-form{
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;                              
                        position: absolute;
                        z-index: 20;
                        overflow: hidden;
                        transition: 0.2s 0.3s ease-in-out;
                        width: 400px;
                        height: 400px;
                        .title{
                            font-size: 2.2rem;
                            color: #424242;
                            margin-bottom: 10px;
                        }
                        .input-field{
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            align-items: center;
                            width: 95%;
                            height: 55px;
                            background-color: #FFFFFF;
                            border: 1px solid #2577f1;
                            margin: 20px 0;
                            border-radius: 10px;
                            justify-content: space-around;
                            .icon{
                                width: 5%;
                                text-align: center;
                                line-height: 55px;
                                color: #000000;
                                font-size: 1.1rem;
                            }
                            input{
                                width: 70%;
                                background: none;
                                outline: none;
                                border: none;
                                line-height: 1;
                                font-size: 1.1rem;
                                color: #000000;
                                &::placeholder{
                                    color: #DDDDDD;
                                }
                            }
                        }
                        .btn{
                            width: 180px;
                            padding: 15px 0;
                            border: none;
                            outline: none;
                            border-radius: 10px;
                            cursor: pointer;
                            font-weight: 600;
                            margin: 10px 0;
                            transition: 0.5s;
                            text-align: center;
                            background-color: #2577f1;
                            border: 2px solid #2577f1;
                            color: #FFFFFF;
                            &:hover{
                                transition: 0.5s;
                                background-color: #FFFFFF;
                                color: #2577f1;
                            }
                        }
                    }
                }
            }// login

            /* ESPECIALITY */
            #especiality{
                width: 100%;
                height: 100vh;
                padding: 0;
                .especiality-content{
                    width: 100%;
                    height: 100vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .header{
                        width: 100%;
                        padding: 10px 0;
                        background-color: #2577f1;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;
                        h1{
                            margin-left: 30px;
                            color: #FFFFFF;
                        }
                        .button-type-6{
                            margin-right: 30px;
                            width: 100px;
                            background-color: #FFFFFF;
                            text-align: center;
                            padding: 20px 10px;
                            color: #2577f1;
                            border-radius: 10px;
                            text-decoration: none;
                            font-weight: bold;
                        }
                    }
                    .especiality-cards{
                        width: 95%;
                        height: auto;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        flex-wrap: wrap;
                        .link-card{
                            text-decoration: none;
                            .card-type-1{
                                margin: 30px 0;
                                width: 380px;
                                height: 260px;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: flex-start;
                                position: relative;
                                overflow: hidden;
                                background-color: #2577f1;
                                border-radius: 10px;
                                transition: all 0.6s;
                                .title-card{
                                    display: flex;
                                    flex-direction: row;
                                    justify-content: center;
                                    align-items: center;
                                    img{
                                        width: 50px;
                                        height: 50px;
                                        margin-left: 20px;
                                        padding: 10px 0;
                                    }
                                    h5{
                                        color: #FFFFFF;
                                        padding: 10px 15px;
                                    }
                                }
                                p{
                                    color: #FFFFFF;
                                    padding: 10px 20px;
                                }
                                .icon-box{
                                    width: 150px;
                                    height: 150px;
                                    position: absolute;
                                    z-index: 10;
                                    bottom: 0;
                                    right: 0;
                                    img{
                                        width: 100%;
                                        height: 100%;
                                        color: #FFFFFF;
                                        opacity: 0.2;
                                    }
                                }
                                &:hover{
                                    transition: all 0.6s;
                                    transform: translateY(-8px);
                                }
                            }
                        }
                    }
                }
            }// especiality

            /* DOCTOR */
            #doctor{
                width: 100%;
                height: 100vh;
                padding: 0;
                .team-doctors{
                    width: 80%;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    align-items: center;
                    flex-wrap: wrap;
                    .link-card{
                        text-decoration: none;
                        .card-type-3{
                            width: 320px;
                            height: 320px;
                            border: 1px solid #EDEDED;
                            margin: 30px 0;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            position: relative;
                            z-index: 0;
                            transition: 0.3s all;
                            .img-team{
                                width: 150px;
                                height: 150px;
                                border-radius: 50%;
                                margin: 30px 0;
                            }
                            .text-team{
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                                h5, p{
                                    color: #000000;
                                }
                            }
                            &:hover{
                                z-index: 1;
                                transition: 0.3s all;
                                border: none;
                                box-shadow: 0 8px 50px rgba(37, 119, 241, 0.2);
                                transform: scale(1.05);
                            }
                        }
                    }
                }
            }// team

            /* SCHEDULE */
            #schedule{
                width: 100%;
                height: 100vh;
                padding: 0;
                h1{
                    color: #2577f1;
                }
                .content-schedule{
                    width: 50%;
                    height: 60%;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-evenly;
                    align-items: center;
                    flex-wrap: wrap;              
                    .button-type-6{
                        width: 200px;
                        margin: 20px 0;
                        padding: 20px 10px;
                        border: 2px solid #2577f1;
                        border-radius: 10px;
                        color: #2577f1;
                        text-align: center;
                        text-decoration: none;
                        font-size: 1.8rem;
                        transition: all 0.4s;
                        font-weight: bold;
                        &:hover{
                            transition: all 0.4s;
                            background-color: #2577f1;
                            border: 2px solid #2577f1;
                            color: #FFFFFF;
                        }
                    }
                }
            }// schedule

            /* WINDOWS TV */
            #window-tv{
                width: 100%;
                height: 100vh;
                padding: 0;
                .content-windows-tv{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .tokens-windows-1{
                        width: 100%;
                        height: 80%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;
                        .video-tv{
                            width: 80%;
                            height: 100%;
                            video{
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                object-position: center center;
                            }
                        }
                        .token-tv{
                            width: 20%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            align-items: center;
                        }
                    }
                    .tokens-windows-2{
                        width: 100%;
                        height: 20%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;
                    }
                }
            }
        }
    }
    