//RESOLUTION TABLET
    @media only screen and (max-width: resolution.$tablet){   
        /*--- HEADER ---*/
        .header{
            width: 100%;
            padding: 20px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .information{
                display: none !important;
                width: 90%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                .info-text{
                    width: 100%;
                    padding: 5px 0;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: center;
                    .info-sub-tex{
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start;
                        align-items: center;
                        .icon{
                            font-size: 1.2rem;
                            color: #2577f1;
                        }
                        p{
                            margin-left: 20px;
                            font-size: 1rem;
                            color: #000000;
                            letter-spacing: 2px;
                        }
                    }
                }
            }
            nav{
                width: 85%;
                height: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                .nav-logo{
                    .logo{                    
                        color: #000000;
                        text-decoration: none;
                    }
                }
                .nav-items{
                    position: absolute;
                    top: 100px;
                    left: 0;        
                    background-color: gray;
                    display: flex;
                    flex-direction: column;
                    width: 100%;
                    height: -webkit-fill-available;        
                    transform: translateX(-100%);
                    transition: 0.3s ease all;
                    z-index: 20;
                    li{
                        list-style: none;
                        z-index: 20;
                        a{
                            color: #2b2b2b;
                            text-decoration: none;
                            font-size: 17px;
                            transition: all 0.2s;
                            font-weight: bold;
                            &:hover{
                                transition: all 0.2s;
                                color: #2577f1;
                            }
                        }
                        &:nth-last-child(1) a{
                            transition: all 0.2s;
                            color: #ffffff;
                        }
                        .btn-header{
                            padding: 15px 20px;
                            text-decoration: none;
                            background-color: #2577f1;
                            border: 1px solid #2577f1;
                            border-radius: 10px;
                            transition: all 0.2s;
                            font-weight: bold;
                            &:hover{
                                transition: all 0.2s;
                                background-color: #FFFFFF;
                                color: #2577f1;
                            }
                        }
                    }
                    &.open{
                        transform: translateX(0) !important
                    }
                }
                .nav-toggle{
                    display: flex !important;
                    flex-direction: column;                
                    margin: 15px;
                    span{
                        width: 30px;
                        height: 4px;
                        background: black;
                        margin-bottom: 5px;
                        border-radius: 2px;             
                        transform-origin: 5px 0px;
                        transition: all 0.2s linear;
                    }
                    &.open > span{
                        transform: rotate(45deg) translate(0px, 0px);
                    }
                    &.open > span:nth-child(2){
                        display: none;
                    }
                    &.open > span:nth-child(3){
                        transform: rotate(-45deg) translate(-5px, 1px);
                    }   
                }

            }
        }// header
    } //resolutionMax

