/*--------------------------
        IMPORTS
--------------------------*/
    // RESOLUTIONS
        @use './resolutions' as resolution;
    // COLORS
        @use './colors' as color;
    // TYPOGRAPHY
        @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
        @import url('https://fonts.googleapis.com/css2?family=Shippori+Mincho&display=swap');
/*----------------
    FONST STYLE
----------------*/
    // FONTS TYPE
        $font-1: 'Arial',sans-serif;
        $font-2: 'Arial',sans-serif;
    // FONTS SIZE
        $title: 48px;
        $subtitle: 36px;
        $text-ultrabig: 36px;
        $text-big: 26px;
        $text-medium: 15px;
        $text-small: 13px;
        $text-ultrasmall: 10px;

    *{
        font-family: $font-1;
    }
/*--------------------------
    RESOLUTION CONTROLLER
--------------------------*/
    //MAX RESOLUTION
        @media only screen and (max-width: resolution.$pc){                     
            // TITLE TEXT
                h1{
                    font-family: $font-1;
                    font-weight: bolder;
                    font-size: $title;
                    margin-bottom: 15px;
                }

            // SUBTITLE TEXT
                h2{
                    font-family: $font-2;
                    font-weight: bolder;
                    font-size: $subtitle;
                    margin-bottom: 7px;
                }

            // EXTRABIG TEXT
                h3{
                    font-family: $font-2;
                    font-weight: bolder;
                    font-size: $text-ultrabig;
                }
                h4{
                    font-family: $font-2;
                    font-weight: bolder;
                    font-style: italic;
                    font-size: $text-ultrabig;
                }

            // BIG TEXT
                h5{
                    font-family: $font-2;
                    font-weight: bolder;
                    font-size: $text-big;
                }
                h6{
                    font-family: $font-2;
                    font-weight: bolder;
                    font-style: italic;
                    font-size: $text-big;
                }
            // MEDIUM TEXT
                th{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                }
                p{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                    letter-spacing: normal;
                    line-height: 1.8;
                }
                li{
                    font-family: $font-1;
                    font-size: $text-medium;
                }
                a{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                    color: color.$P-100;
                    &.line-hover{
                        position: relative;
                        &::before{
                            content: '';
                            position: absolute;
                            width: 0%;
                            bottom: -3px;
                            left: 0;
                            height: 3px;
                            background-color: color.$P-100;
                            transition-duration: 0.8s;
                        }
                        &:hover,
                        &.active{
                            &::before{
                                width: 100%;
                            }
                        }
                    }
                    &.title{
                        font-size: $text-big - 5px;
                    }
                }
                button{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                }
                textarea{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                }
                input{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                }
                label{
                    font-family: $font-1;
                    font-size: $text-medium;
                    text-align: justify;
                }

            // SMALL TEXT
                i{
                    font-family: $font-1;
                    font-size: $text-big - 7px;
                    text-align: justify;
                }
                span{
                    font-family: $font-1;
                    font-size: $text-ultrasmall;
                    text-align: justify;
                }
        } //resolutionMax
        
    //RESOLUTION LAPTOP
        @media only screen and (max-width: resolution.$laptop){ 
        } //resolutionMax1280px
        
    //RESOLUTION TABLET
        @media only screen and (max-width: resolution.$tablet) {   
        } //resolutionMax960px

    //RESOLUTION MOVIL
        @media only screen and (max-width: resolution.$mobile){ 
            // TITLE TEXT
            h1{
                font-family: $font-1;
                font-weight: bolder;
                font-size: $title - 20px;
                margin-bottom: 15px;
            }
            
        } //resolutionMax500px
