    html{
        width: 100%;
        height: auto;
        overflow: inherit;
        overflow-x: hidden;
        background-color: #FFFFFF;
        body{        
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            #root{
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }// root

            /*--- BANNER ---*/
            #banner{
                width: 100%;
                height: 100vh;
                padding: 0;
                .conten-banner{
                    width: 100%;
                    height: 100vh;
                    .swp-banner{
                        width: 100%;
                        height: 100%;
                        .swp-slide-banner{
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            .text-banner{
                                width: 85%;
                                height: 90%;
                                display: flex;
                                flex-direction: row;
                                justify-content: space-between;
                                align-items: center;
                                position: absolute;
                                z-index: 20;
                                .text-box{
                                    width: 50%;
                                    padding: 50px 0;
                                    border-left: 15px solid #2577f1;
                                    background-color: #111111;
                                    h5, h1, p{
                                        padding: 20px 0;
                                        margin: 0 25px;
                                        color: #FFFFFF;
                                    }
                                    h1{
                                        margin-bottom: 40px;
                                        strong{
                                            color: #2577f1;
                                        }
                                    }
                                    .button-type-1{
                                        margin-left: 25px;
                                        padding: 15px 25px;
                                        background-color: #2577f1;
                                        border-radius: 10px;
                                        text-decoration: none;
                                        font-weight: bold;
                                        border: 2px solid #2577f1;
                                        transition: all 0.4s;
                                        &:hover{
                                            background-color: #FFFFFF;
                                            color: #2577f1;
                                            transition: all 0.4s;
                                        }
                                    }
                                }
                            }
                            .img-banner{
                                width: 50%;
                                height: 100%;
                            }
                        }
                        .swiper-button-prev{
                            margin-left: 50px;
                            &::after{
                                color: #2577f1;    
                            }
                        }
                        .swiper-button-next{
                            margin-right: 50px;
                            &::after{
                                color: #2577f1;    
                            }
                        }
                        .swiper-button-disabled{
                            color: #2577f1;
                        }
                    }
                }
            }// banner
            
            /*--- ABOUT ---*/
            #about{
                width: 100%;
                padding: 0;
                .content-about{
                    width: 100%;
                    height: 100vh;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    border-top: 1px solid #EDEDED;
                    .text-about{
                        width: 35%;
                        height: 80%;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-start;
                        align-items: flex-start;
                        h5, h1, p{
                            padding: 0 50px;
                            width: 85%;
                            margin: 15px 0;
                        }
                        p{
                            font-size: 1.2rem;
                        }
                        .button-type-1{
                            margin-left: 50px;
                            padding: 15px 50px;
                            background-color: #2577f1;
                            border-radius: 10px;
                            text-decoration: none;
                            font-weight: bold;
                            border: 2px solid #2577f1;
                            transition: all 0.4s;
                            &:hover{
                                background-color: #FFFFFF;
                                color: #2577f1;
                                transition: all 0.4s;
                            }
                        }
                    }
                    .img-about{
                        width: 50%;
                        height: 100%;
                        position: relative;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        .img-box{
                            position: absolute;
                            left: 0;
                            bottom: 0;
                            z-index: 20;
                            position: absolute;
                            img{
                                width: 100%;
                                height: 100%;
                            }
                        }
                        .box{
                            width: 400px;
                            height: 400px;
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            z-index: 10;
                            background-color: #2577f1;
                        }
                    }
                }
