            /*--- DEPARTMENTS ---*/
            #departments{
                width: 100%;
                height: auto;
                padding: 80px 0;
                .content-departments{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    padding: 30px 0;
                    .cards-departments{
                        width: 90%;
                        padding: 30px 0;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        flex-wrap: wrap;
                        .link-card{
                            text-decoration: none;
                            .card-type-1{
                                width: 300px;
                                height: 350px;
                                background-color: #FFFFFF;
                                margin: 30px 15px;
                                border-radius: 10px;
                                box-shadow: 0 2px 10px rgba(37, 119, 241, 0.24);
                                position: relative;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                                transition: all 0.3s ease;
                                border-radius: 20px;
                                .img-servicie{
                                    width: 100%;
                                    height: 100%;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    align-items: center;
                                    background-color: #2577f1;
                                    border-radius: 20px;
                                    img{
                                        width: 130px;
                                        height: 130px;
                                    }
                                    h5{
                                        color: #FFFFFF;
                                        margin: 20px 0;
                                        font-size: 1.8rem;
                                    }
                                    img, h5{
                                        opacity: 0.4;
                                        padding: 5px 0;
                                        transition: all 0.5s;
                                    }
                                }
                                &:hover{
                                    transform: scale(1.05);
                                    .img-servicie{
                                        img, h5{
                                            opacity: 1;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }// departments

            /*--- ATTENTION ---*/
            #attention{
                width: 100%;
                height: 100vh;
                padding: 80px 0;
                .content-attention{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    .content-icon-text{
                        width: 90%;
                        height: 40%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        .box-icon-text{
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            width: 30%;
                            height: 250px;
                            border-radius: 10px;
                            margin-top: 40px;
                            transition: all 0.6s;
                            .icon{
                                font-size: 3rem;
                                margin: 20px 0;
                                color: #2577f1;
                            }
                            p{
                                width: 90%;
                                color: #000000;
                                text-align: center;
                                font-size: 1rem;
                                transition: all 0.6s;
                            }
                            &:hover{
                                transition: all 0.6s;
                                transform: translateY(-8px);
                                background-color: #EDEDED;
                                border-bottom: 4px solid #2577f1;
                            }
                        }
                    }
                }
            }// attention
        }// body
