/*--------------------------
        IMPORTS
--------------------------*/
@use './../../../base/config';

html{
    width: 100%;
    height: auto;
    overflow: inherit;
    overflow-x: hidden;
    background-color: #FFFFFF;
    body{
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        #root{
            width: 100%;
            height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
        }// root

        .form-atentions, .form-recipes{
            .tss-58la38-MUIDataTable-paper{
                border: 1px solid rgba(224, 224, 224, 1);
                .tss-tlx3x1-MUIDataTableToolbar-root{
                    display: none;
                }
                .tss-1ork7hi-MUIDataTablePagination-tableCellContainer{
                    display: none;
                }
            }   
        }

        .form-lab, .form-plac{
            .card-type-1{
                width: 100%;
                height: 300px;
                background-color: #FFFFFF;
                border-radius: 10px;
                box-shadow: 0 2px 10px rgba(37, 119, 241, 0.24);
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                transition: all 0.3s ease;
                border-radius: 20px;
                .img-servicie{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    background-color: #2577f1;
                    border-radius: 20px;
                    h5{
                        color: #FFFFFF;
                        margin: 20px 0;
                        font-size: 1.8rem;
                        text-align: center;
                    }
                    img, h5{
                        padding: 5px 0;
                    }
                }
            }
            .content-lab{
                border: 1px solid #2577f1;
                border-top: 6px solid #2577f1;  
            }
        }
    }
}
