/*--------------------------
        GENERAL
--------------------------*/
*{
    margin: 0;
    padding: 0;
}
html{
    min-width: 100vw;
    min-height: 100vh;
    body{
        position: static;
        min-width: 100vw;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        #root{
            min-width: 100vw;
            min-height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
        }
        div {
            img{
                width: 100%;
                height: auto;
            }
        }
        section{
            width: 100%;
            padding: 35px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            &.row{
                flex-direction: row;
            }
            &.column{
                flex-direction: column;
            }
        }
        .css-ee20y-MuiPaper-root{
            width: 100%;
        }
        .tss-1acbflw-MUIDataTable-responsiveScrollMaxHeight{
            max-height: 600px !important;
        }
        input[type="number"]{
            -webkit-appearance: textfield !important;
            margin: 0;
            -moz-appearance:textfield !important;
        }
        fieldset.MuiFormControl-root{
            padding: 25px;
        }
        .css-ubpweo-MuiPaper-root-MuiPopover-paper{
            right: 15px;
            left: auto !important;
            width: max-content;
        }
        .tss-ynxllk-MUIDataTableFilter-root{
            width: 900px;
        }
    }
}
