/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../../../base/config';
    @use './../../../../base/typography';
    // @use './../../../../components/button/style';

    html{
        width: 100%;
        height: auto;
        overflow: inherit;
        overflow-x: hidden;
        background-color: #FFFFFF;
        body{        
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            #root{
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
            }// root

            /*--- BANNER ---*/
            #banner-servicies{
                width: 100%;
                padding: 0;
                .content-banner{
                    width: 100%;
                    height: 70vh;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    .bg-banner-about{
                        width: 100%;
                        height: 100%;
                        position: relative;
                        top: 0;
                        left: 0;
                        z-index: -10;
                        overflow: hidden;
                        background-color: #EDEDED;
                        &::before{
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            z-index: 10;
                            background-color: rgba(255, 255, 255, 0.5);
                        }
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            object-position: center center;
                        }
                    }
                    .text-banner-about{
                        width: 85%;
                        height: 80%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        z-index: 20;
                    }
                }
            }// banner

            /*--- SERVICIES ---*/
            #servicies{
                width: 100%;
                height: auto;
                padding: 130px 0;
                .content-servicies{
                    width: 90%;
                    height: auto;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    margin: 30px 0;
                    .servicies{
                        width: 90%;
                        padding: 20px 0;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        align-items: center;
                        flex-wrap: wrap;
                        .card-type-4{
                            width: 300px;
                            height: 350px;
                            background-color: #FFFFFF;
                            margin: 30px 15px;
                            border-radius: 10px;
                            box-shadow: 0 2px 10px rgba(37, 119, 241, 0.24);
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;
                            transition: all 0.3s ease;
                            border-radius: 20px;
                            .img-servicie{
                                width: 100%;
                                height: 100%;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;
                                background-color: #2577f1;
                                border-radius: 20px;
                                img{
                                    width: 130px;
                                    height: 130px;
                                }
                                h5{
                                    color: #FFFFFF;
                                    margin: 20px 0;
                                    font-size: 1.8rem;
                                }
                                img, h5{
                                    opacity: 0.4;
                                    padding: 5px 0;
                                    transition: all 0.5s;
                                }
                            }
                            &:hover{
                                transform: scale(1.05);
                                .img-servicie{
                                    img, h5{
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                    }
                }
            }// servicies
        }// body
    }// html