/*--------------------------
        IMPORTS
--------------------------*/
@use './../../../base/config';
@use './../../../base/typography';
// @use './../../../components/button/style';

html{
    width: 100%;
    height: auto;
    overflow: inherit;
    overflow-x: hidden;
    background-color: #FFFFFF;
    body{
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        #root{
            width: 100%;
            height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
        }// root

        // Estilos para Search
        .MuiBox-root{
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            .MuiFormControl-root{
                width: 450px;
                margin: 0 20px;
            }
            button{
                padding: 10px 30px;
            }
        }
        .card-search{
            text-decoration: none;
            padding: 0 !important;
            height: auto !important;
            text-transform: none;
            .card-s{
                background-color: rgb(237, 247, 237);
                &:hover{
                    transition: all 0.6s;
                    transform: translateY(-8px);
                }
            }                
            &:hover{
                background: transparent;
            }
        }
        .card-insured{
            border-radius: 10px;
            border: 1px solid #2577f1;
            border-bottom: 5px solid #2577f1;
        }

        /* ESPECIALITY */
        #especialty-tokens{
            width: 95%;
            height: 60vh;
            padding: 0;
            .content{
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .swp-especialty-tokens{
                    width: 100%;
                    height: 100%;
                }
            }
            .content-row{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
            }
            .link-card{
                text-decoration: none;
                .card-type-1{
                    margin: 30px 0;
                    width: 320px;
                    height: 260px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    position: relative;
                    overflow: hidden;
                    background-color: #2577f1;
                    border-radius: 10px;
                    transition: all 0.6s;
                    .title-card{
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        img{
                            width: 50px;
                            height: 50px;
                            margin-left: 20px;
                            padding: 10px 0;
                        }
                        h5{
                            color: #FFFFFF;
                            padding: 10px 15px;
                        }
                    }
                    p{
                        color: #FFFFFF;
                        padding: 10px 20px;
                    }
                    .icon-box{
                        width: 150px;
                        height: 150px;
                        position: absolute;
                        z-index: 10;
                        bottom: 0;
                        right: 0;
                        img{
                            width: 100%;
                            height: 100%;
                            color: #ffffff;
                            opacity: 0.2;
                        }
                    }
                    &:hover{
                        transition: all 0.6s;
                        transform: translateY(-8px);
                    }
                }
                &:hover{
                    background: transparent;
                }
            }
        }// especiality
        
        /* DOCTOR */
        #doctor-tokens{
            width: 95%;
            height: 60vh;
            padding: 0;
            .content{
                width: 100%;
                height: 100%;
                .swp-doctor-tokens{
                    width: 100%;
                    height: 100%;
                }
            }
            .content-row{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
            }
            .link-card{
                text-decoration: none;
                .card-type-3{
                    width: 320px;
                    height: 320px;
                    border: 1px solid #EDEDED;
                    margin: 30px 0;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    position: relative;
                    z-index: 0;
                    transition: 0.3s all;
                    .img-team{
                        width: 150px;
                        height: 150px;
                        border-radius: 50%;
                        margin: 30px 0;
                    }
                    .text-team{
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        h5, p{
                            color: #000000;
                        }
                    }
                    &:hover{
                        z-index: 1;
                        transition: 0.3s all;
                        border: none;
                        box-shadow: 0 8px 50px rgba(37, 119, 241, 0.2);
                        transform: scale(1.05);
                    }
                }
                &:hover{
                    background: transparent;
                }
            }
        }// doctor

        /* SCHEDULE */
        #schedule-tokens{
            width: 100%;
            height: 60vh;
            padding: 0;
            .btn-tokens{
                padding: 15px 20px;
                text-decoration: none;
                background-color: #2577f1;
                border: 1px solid #2577f1;
                border-radius: 10px;
                transition: all 0.2s;
                font-weight: bold;
                &:hover{
                    transition: all 0.2s;
                    background-color: #FFFFFF;
                    color: #2577f1;
                }
            }
        }// schedule
    }
}
