/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../base/colors' as color;
    @use './../../base/resolutions' as resolution;
/*--------------------------
    RESOLUTION CONTROLLER
--------------------------*/
    //MAX RESOLUTION
    @media only screen and (max-width: resolution.$pc){  
        /*--- FOOTER ---*/
        footer{
            width: 100%;
            height: 32vh;
            background-color: #2577f1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding-top: 25px;
            .content-footer{
                width: 80%;
                height: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
                .about-footer{
                    width: 23%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    h1, p{
                        color: #FFFFFF;
                    }
                }
                .list-footer{
                    width: 20%;
                    height: 80%;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: center;
                    h5{
                        color: #FFFFFF;
                        width: 100%;
                    }
                    .sublist{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-around;
                        align-items: flex-start;
                        li{
                            list-style: none;
                            font-size: 17px;
                            color: #FFFFFF;
                            a{
                                text-decoration: none;
                                font-size: 17px;
                                letter-spacing: 2px;
                                &:hover{
                                    text-decoration: underline;
                                }
                            }
                        }
                    }
                }
            }
            .copy{
                width: 100%;
                padding: 10px 0;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                background-color: #2577f1;
                p{
                    color: #FFFFFF;
                    font-size: 14px;
                    a{
                        text-decoration: none;
                        font-size: 17px;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
            }
        }// footer
    } //resolutionMax

