//RESOLUTION MOVIL
    @media only screen and (max-width: resolution.$mobile) {   
        /*--- FOOTER ---*/
        footer{
            width: 100%;
            height: auto;
            background-color: #2577f1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding-top: 25px;
            .content-footer{
                width: 80%;
                height: auto;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                .about-footer{
                    width: 90%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;
                    h1, p{
                        color: #FFFFFF;
                    }
                }
                .list-footer{
                    width: 90%;
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    margin: 20px 0;
                    h5{
                        color: #FFFFFF;
                        width: 100%;
                        text-align: center;
                    }
                    .sublist{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        li{
                            list-style: none;
                            font-size: 17px;
                            margin: 5px 0;
                            color: #FFFFFF;
                            a{
                                text-decoration: none;
                                font-size: 17px;
                                letter-spacing: 2px;
                                &:hover{
                                    text-decoration: underline;
                                }
                            }
                        }
                    }
                }
            }
            .copy{
                width: 100%;
                padding: 10px 0;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                background-color: #2577f1;
                p{
                    color: #FFFFFF;
                    font-size: 14px;
                    a{
                        text-decoration: none;
                        font-size: 17px;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
            }
        }// footer
    } //resolutionMovil
