@media only screen and (max-width: 3550px) {
  /*--- FOOTER ---*/
  footer {
    width: 100%;
    height: 32vh;
    background-color: #2577f1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 25px;
  }
  footer .content-footer {
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  footer .content-footer .about-footer {
    width: 23%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  footer .content-footer .about-footer h1, footer .content-footer .about-footer p {
    color: #FFFFFF;
  }
  footer .content-footer .list-footer {
    width: 20%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  footer .content-footer .list-footer h5 {
    color: #FFFFFF;
    width: 100%;
  }
  footer .content-footer .list-footer .sublist {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
  }
  footer .content-footer .list-footer .sublist li {
    list-style: none;
    font-size: 17px;
    color: #FFFFFF;
  }
  footer .content-footer .list-footer .sublist li a {
    text-decoration: none;
    font-size: 17px;
    letter-spacing: 2px;
  }
  footer .content-footer .list-footer .sublist li a:hover {
    text-decoration: underline;
  }
  footer .copy {
    width: 100%;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #2577f1;
  }
  footer .copy p {
    color: #FFFFFF;
    font-size: 14px;
  }
  footer .copy p a {
    text-decoration: none;
    font-size: 17px;
  }
  footer .copy p a:hover {
    text-decoration: underline;
  }
}
@media only screen and (max-width: 500px) {
  /*--- FOOTER ---*/
  footer {
    width: 100%;
    height: auto;
    background-color: #2577f1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 25px;
  }
  footer .content-footer {
    width: 80%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  footer .content-footer .about-footer {
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  footer .content-footer .about-footer h1, footer .content-footer .about-footer p {
    color: #FFFFFF;
  }
  footer .content-footer .list-footer {
    width: 90%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
  }
  footer .content-footer .list-footer h5 {
    color: #FFFFFF;
    width: 100%;
    text-align: center;
  }
  footer .content-footer .list-footer .sublist {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  footer .content-footer .list-footer .sublist li {
    list-style: none;
    font-size: 17px;
    margin: 5px 0;
    color: #FFFFFF;
  }
  footer .content-footer .list-footer .sublist li a {
    text-decoration: none;
    font-size: 17px;
    letter-spacing: 2px;
  }
  footer .content-footer .list-footer .sublist li a:hover {
    text-decoration: underline;
  }
  footer .copy {
    width: 100%;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #2577f1;
  }
  footer .copy p {
    color: #FFFFFF;
    font-size: 14px;
  }
  footer .copy p a {
    text-decoration: none;
    font-size: 17px;
  }
  footer .copy p a:hover {
    text-decoration: underline;
  }
}
