/*--------------------------
        IMPORTS
--------------------------*/
    @use './../../../base/config';
    @use './../../../base/typography';
    @use './../../../components/button/style';

    @use './../../../base/resolutions' as resolution;

// MAX RESOLUTION PC
    @media only screen and (max-width: resolution.$pc){
        #login{
            width: 100%;
            height: 100vh;
            padding: 0;
            .login-container{
                    position: relative;
                    width: 100%;
                    min-height: 100vh;
                    background-color: #FFFFFF;
                    overflow: hidden;
                    .forms-container{
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        top: 0;
                        left: 0;
                        .signin-signup{
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            align-items: center;
                            position: absolute;
                            top: 50%;
                            left: 95%;
                            transform: translate(-60%, -50%);
                            width: 50%;
                            justify-content: space-between;
                            z-index: 5;
                            transition: 1s 0.7s ease-in-out;
                            .sign-in-form{
                                opacity: 0;
                                display: none;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;                              
                                z-index: 1;
                                overflow: hidden;
                                transition: 0.2s 0.3s ease-in-out;
                                width: 400px;
                                height: 400px;
                                .title{
                                    font-size: 2.2rem;
                                    color: #424242;
                                    margin-bottom: 10px;
                                }
                                .input-field{
                                    display: flex;
                                    flex-direction: row;
                                    justify-content: center;
                                    align-items: center;
                                    max-width: 380px;
                                    width: 100%;
                                    height: 55px;
                                    background-color: #FFFFFF;
                                    border: 1px solid #2577f1;
                                    margin: 10px 0;
                                    border-radius: 10px;
                                    justify-content: space-around;
                                    .icon{
                                        width: 5%;
                                        text-align: center;
                                        line-height: 55px;
                                        color: #000000;
                                        font-size: 1.1rem;
                                    }
                                    input{
                                        width: 70%;
                                        background: none;
                                        outline: none;
                                        border: none;
                                        line-height: 1;
                                        font-size: 1.1rem;
                                        color: #000000;
                                        &::placeholder{
                                            color: #DDDDDD;
                                        }
                                    }
                                }
                                .btn{
                                    width: 150px;
                                    height: 50px;
                                    border: none;
                                    outline: none;
                                    border-radius: 10px;
                                    cursor: pointer;
                                    font-weight: 600;
                                    margin: 10px 0;
                                    transition: 0.5s;
                                    text-align: center;
                                    background-color: #2577f1;
                                    border: 2px solid #2577f1;
                                    color: #FFFFFF;
                                    &:hover{
                                        transition: 0.5s;
                                        background-color: #FFFFFF;
                                        color: #2577f1;
                                    }
                                }
                                .social-text{
                                    padding: 10px 0;
                                    font-size: 1.2em;
                                }
                                .social-media{
                                    width: 100%;
                                    display: flex;
                                    flex-direction: row;
                                    justify-content: center;
                                    align-items: center;                                
                                    .social-icon{
                                        width: 50px;
                                        height: 50px;
                                        border: 1px solid #000000;
                                        border-radius: 50px;
                                        margin: 0 5px;
                                        text-decoration: none;
                                        transition: 0.3s;
                                        i{
                                            font-size: 1.1rem;
                                            color: #000000;
                                        }
                                        &:hover{
                                            border-color: #2271CD;
                                            i{
                                                color: #2271CD;
                                            }
                                        }
                                    }
                                }
                            }
                            .sign-up-form{
                                opacity: 0;
                                display: none;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;                                
                                z-index: 1;
                                overflow: hidden;
                                transition: 0.2s 0.3s ease-in-out;
                                width: 400px;
                                height: 400px;
                                .title{
                                    font-size: 2.2rem;
                                    color: #424242;
                                    margin-bottom: 10px;
                                }
                                .input-field{
                                    display: flex;
                                    flex-direction: row;
                                    justify-content: center;
                                    align-items: center;
                                    max-width: 380px;
                                    width: 100%;
                                    height: 55px;
                                    background-color: #FFFFFF;
                                    border: 1px solid #2577f1;
                                    margin: 20px 0;
                                    border-radius: 10px;
                                    justify-content: space-around;
                                    .icon{
                                        width: 5%;
                                        text-align: center;
                                        line-height: 55px;
                                        color: #000000;
                                        font-size: 1.1rem;
                                    }
                                    input{
                                        width: 70%;
                                        background: none;
                                        outline: none;
                                        border: none;
                                        padding: 5px 0;
                                        line-height: 1;
                                        font-size: 1.1rem;
                                        color: #000000;
                                        &::placeholder{
                                            color: #DDDDDD;
                                        }
                                    }
                                }
                                .btn{
                                    width: 150px;
                                    height: 50px;
                                    border: none;
                                    outline: none;
                                    border-radius: 10px;
                                    cursor: pointer;
                                    font-weight: 600;
                                    margin: 10px 0;
                                    transition: 0.5s;
                                    text-align: center;
                                    background-color: #2577f1;
                                    border: 2px solid #2577f1;
                                    color: #FFFFFF;
                                    &:hover{
                                        transition: 0.5s;
                                        background-color: #FFFFFF;
                                        color: #2577f1;
                                    }
                                }
                                .social-text{
                                    padding: 10px 0;
                                    font-size: 1.2em;
                                }
                                .social-media{
                                    width: 100%;
                                    .social-icon{
                                        width: 50px;
                                        height: 50px;
                                        border: 1px solid #000000;
                                        border-radius: 50px;
                                        margin: 0 5px;
                                        text-decoration: none;
                                        transition: 0.3s;
                                        i{
                                            font-size: 1.1rem;
                                            color: #000000;
                                        }
                                        &:hover{
                                            border-color: #2271CD;
                                            i{
                                                color: #2271CD;
                                            }
                                        }
                                    }
                                }
                            }
                            .sign-up-form.active,
                            .sign-in-form.active{
                                display: flex;
                                opacity: 1;
                            }
                        }
                    }
                    &::before{
                        content: "";
                        position: absolute;
                        width: 2000px;
                        height: 2000px;
                        border-radius: 50%;
                        top: -10%;
                        right: 50%;
                        transform: translateY(-50%);
                        z-index: 6;
                        background: linear-gradient(-45deg, #2577f1, #2577f1);
                        transition: 1.3s ease-in-out;
                    }
                    .panels-container{
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        top: 0;
                        left: 0;
                        .panel{
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            width: 100%;
                            align-items: flex-end;
                            justify-content: space-around;  
                            text-align: center;
                            z-index: 7;
                            .content{
                                color: #FFFFFF;
                                transition: 0.9s 0.7s ease-in-out;
                                h3{
                                    font-weight: 600;
                                    font-size: 25px;
                                    line-height: 1;
                                    margin-bottom: 25px;
                                }
                                p{
                                    font-size: 25px;
                                    text-align: center;
                                    padding: 10px 0;
                                }
                                .btn.transparent{
                                    margin: 0;
                                    background: none;
                                    border: 2px solid #FFFFFF;
                                    width: 120px;
                                    height: 50px;
                                    border-radius: 10px;
                                    font-weight: 600;
                                    font-size: 1em;
                                    color: #FFFFFF;
                                    text-align: center;
                                    cursor: pointer;
                                    margin-bottom: 35px;
                                    &:hover{
                                        background-color: #FFFFFF;
                                        border: 2px solid #2577f1;
                                        color: #2577f1;
                                    }
                                }
                            }
                            .image{
                                width: 125%;
                                transition: 1.1s 0.4s ease-in-out;
                                position: relative;         
                                z-index: 10;
                            }
                        }
                        .left-panel{    
                            opacity: 1;
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-end;
                            align-items: center;
                            width: 45%;
                            height: 100%;
                            pointer-events: all;
                            transition-duration: 1.5s;
                            img{
                                bottom: -5px;
                            }
                        }
                        .right-panel{
                            opacity: 0;
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-end;
                            align-items: center;
                            width: 45%; 
                            height: 100%;
                            pointer-events: none;
                            transition-duration: 1.5s;
                            transform: translateX(80%);
                        }
                    }
                    /* ANIMATION */
                    &.sign_up_mode{
                        .left-panel{
                            pointer-events: none;
                            .content, .image{
                                transform: translateX(-800px);
                            }
                        }
                    }
                    &.sign_up_mode{
                        .right-panel{
                            pointer-events: all;
                            .content, .image{
                                transform: translateX(0px);
                            }
                        }
                    }
                    &.sign_up_mode::before{
                        transform: translate(100%, -50%);
                        right: 50%;
                    }
                    &.sign_up_mode .signin-signup{
                        left: 35%;
                    }
            }
            &.doctor{
                .login-container{
                        .forms-container{
                            .signin-signup{
                                transform: translate(-30%, -50%);
                            }
                        }
                        .panels-container{
                            .left-panel{
                                opacity: 0;
                                transition-duration: 1.5s;
                            }
                            .right-panel{
                                opacity: 1;
                                transform: translateX(0%);
                                transition-duration: 1.5s;
                            }
                        }
                }
            }
        }
    }// MaxResolution PC

